/* Modal Overlay */
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7); /* Fondo semitransparente */
    display: none; /* Por defecto, el modal está oculto */
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  /* Mostrar el modal cuando la clase "active" está presente */
  .modal.active {
    display: flex;
    color: black;
  }
